.success {
    border: solid 2px #007400;
    border-radius: 4px;
    background-color: #d3d3d3;
    color: #007400;
    display: flex;
    height: 3rem;
    font-size: 2rem;
    align-items: center;
    padding-left: 1rem;
}
.error {
    border: solid 2px #ff0000;
    border-radius: 4px;
    background-color: #d3d3d3;
    color: #ff0000;
    display: flex;
    height: 3rem;
    font-size: 2rem;
    align-items: center;
    padding-left: 1rem;
}